@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  font-family: "Roboto Condensed" sans-serif;
}


:root {
    --main-text-color: #fff;
    --primary-blue: blue;
    --primary-dark: rgb(255, 255, 255, 0)
  }
.navbar-nav > li > a{
    color: #fff !important;
    color: var(--main-text-color) !important;
    font-family: 'Roboto Condensed';
    letter-spacing: 0.07rem;
    border-top: 0.18rem solid rgb(255, 255, 255, 0);
    border-top: 0.18rem solid var(--primary-dark);
}

.navbar-nav > li > a.active, .navbar-nav > li > a.active:focus {
    border-top: 0.18rem solid blue;
    border-top: 0.18rem solid var(--primary-blue);
}
.navbar-nav > li > a:hover{
    border-top: 0.18rem solid blue;
    border-top: 0.18rem solid var(--primary-blue);
}

